          <!-- Language Switcher (Desktop) -->
          <div class="relative" id="language-switcher" ref="languageMenuRef">
            <button
              class="p-2 rounded-md text-slate-500 hover:text-slate-700 hover:bg-slate-100 transition-colors flex items-center"
              @click="isLanguageDropdownOpen = !isLanguageDropdownOpen"
            >
              <img
                src="https://unpkg.com/lucide-static@latest/icons/globe.svg"
                alt="language"
                class="w-5 h-5 mr-1"
              />
              <span class="text-xs">{{ currentLocaleName }}</span>
              <img
                src="https://unpkg.com/lucide-static@latest/icons/chevron-down.svg"
                alt="open"
                class="w-3.5 h-3.5 ml-0.5"
              />
            </button>
            <div
              class="absolute right-0 mt-1 py-1 w-28 bg-white border border-slate-200 rounded-md shadow-lg"
              :class="{ hidden: !isLanguageDropdownOpen }"
            >
              <a
                class="block px-4 py-1.5 text-sm font-medium"
                :class="{
                  'text-primary-600': currentLocale === 'en',
                  'bg-primary-50': currentLocale === 'en',
                  'hover:bg-slate-50': currentLocale !== 'en',
                }"
                lang="en"
                @click="changeLocale"
              >
                English
              </a>
              <a
                class="block px-4 py-1.5 text-sm font-medium"
                :class="{
                  'text-primary-600': currentLocale === 'zh',
                  'bg-primary-50': currentLocale === 'zh',
                  'hover:bg-slate-50': currentLocale !== 'zh',
                }"
                lang="zh"
                @click="changeLocale"
              >
                中文
              </a>
            </div>
          </div>

          <!-- Theme Toggle (placeholder) -->
          <button
            class="p-2 rounded-md text-slate-500 hover:text-slate-700 hover:bg-slate-100 transition-colors"
