        </div>

        <!-- Mobile Menu Button -->
        <div class="md:hidden flex items-center">
          <button
            class="p-2 rounded-md text-slate-500 hover:text-slate-700 hover:bg-slate-100 transition-colors"
            @click="isMobileMenuOpen = !isMobileMenuOpen"
            ref="mobileMenuButtonRef"
          >
            <img
              src="https://unpkg.com/lucide-static@latest/icons/menu.svg"
              alt="菜单"
              class="w-6 h-6"
            />
          </button>
        </div>
      </div>

      <!-- Mobile Navigation Menu (Hidden by default) -->
      <div id="mobile-menu" :class="{ 'hidden': !isMobileMenuOpen }" class="md:hidden pb-3" ref="mobileMenuRef">
        <div class="flex flex-col space-y-1">
          <!-- <a
            href="index.html"
            class="px-4 py-2 text-base font-medium text-primary-600 bg-primary-50 rounded-md"
            >首页</a
          >
          <a
