          <!-- Language Switcher (Mobile) -->
          <div
            class="px-4 py-2 text-base font-medium text-slate-700 hover:bg-slate-100 rounded-md"
          >
            <div class="flex items-center justify-between">
              <div class="flex items-center">
                <img
                  src="https://unpkg.com/lucide-static@latest/icons/globe.svg"
                  alt="语言"
                  class="w-5 h-5 mr-2"
                />
                <span>{{ t("language") }}</span>
              </div>
            </div>
            <div class="mt-2 pl-7 flex space-x-2">
              <button
                class="px-3 py-1 text-sm rounded font-medium"
                :class="{
                  'bg-primary-50 text-primary-600': currentLocale === 'zh',
                  'border border-slate-200 text-slate-600 hover:bg-slate-50': currentLocale !== 'zh'
                }"
                @click="changeLocaleMobile('zh')"
              >
                中文
              </button>
              <button
                class="px-3 py-1 text-sm rounded"
                :class="{
                  'bg-primary-50 text-primary-600': currentLocale === 'en',
                  'border border-slate-200 text-slate-600 hover:bg-slate-50': currentLocale !== 'en'
                }"
                @click="changeLocaleMobile('en')"
              >
                English
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </nav>
</template>

<script setup>
import { ref, computed, onMounted, onUnmounted } from "vue";
const { setLocale, locale } = useI18n();

const { t } = useI18n({
  useScope: "local",
});

const currentLocale = ref(locale.value);
// 控制语言下拉菜单的显示状态
const isLanguageDropdownOpen = ref(false);
// 控制移动端菜单显示状态
const isMobileMenuOpen = ref(false);

// 引用DOM元素
const languageMenuRef = ref(null);
const mobileMenuRef = ref(null);
const mobileMenuButtonRef = ref(null);

console.log(currentLocale.value);

const currentLocaleName = computed(() => {
  return currentLocale.value === "en" ? "English" : "中文";
});

const changeLocale = async (event) => {
  event.target.blur();
  isLanguageDropdownOpen.value = false;
  const locale = event.target.attributes.lang.value;
  currentLocale.value = locale;
  await setLocale(locale);
};

const changeLocaleMobile = async (locale) => {
  currentLocale.value = locale;
  await setLocale(locale);
  // Optionally close the mobile menu after changing language
  isMobileMenuOpen.value = false;
};

// 点击外部区域关闭菜单的处理函数
const handleClickOutside = (event) => {
  // 处理语言切换菜单
  if (
    languageMenuRef.value && 
    !languageMenuRef.value.contains(event.target) && 
    isLanguageDropdownOpen.value
  ) {
    isLanguageDropdownOpen.value = false;
  }
  
  // 处理移动端菜单
  if (
    mobileMenuRef.value && 
    mobileMenuButtonRef.value && 
    !mobileMenuRef.value.contains(event.target) && 
    !mobileMenuButtonRef.value.contains(event.target) && 
    isMobileMenuOpen.value
  ) {
    isMobileMenuOpen.value = false;
  }
};

// 组件挂载时添加事件监听
onMounted(() => {
  document.addEventListener('click', handleClickOutside);
});

// 组件卸载时移除事件监听
onUnmounted(() => {
  document.removeEventListener('click', handleClickOutside);
});
</script>

